import Vue from 'vue'
import Router from 'vue-router'
import StronaGlowna from '@/components/StronaGlowna'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'StronaGlowna',
      component: StronaGlowna
    }
  ]
})



